body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
  box-sizing: border-box;
	vertical-align: baseline;
}

/* make sure to set some focus styles for accessibility */
:focus {
    outline: 0;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

body {
	line-height: 1;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
    -webkit-appearance: none;
    -moz-appearance: none;
}

input[type=search] {
    -webkit-appearance: none;
    -moz-appearance: none;
    box-sizing: content-box;
}

textarea {
    overflow: auto;
    vertical-align: top;
    resize: vertical;
}

/**
 * Correct `inline-block` display not defined in IE 6/7/8/9 and Firefox 3.
 */

audio,
canvas,
video {
    display: inline-block;
    *display: inline;
    *zoom: 1;
    max-width: 100%;
}

/**
 * Prevent modern browsers from displaying `audio` without controls.
 * Remove excess height in iOS 5 devices.
 */

audio:not([controls]) {
    display: none;
    height: 0;
}

/**
 * Address styling not present in IE 7/8/9, Firefox 3, and Safari 4.
 * Known issue: no IE 6 support.
 */

[hidden] {
    display: none;
}

/**
 * 1. Correct text resizing oddly in IE 6/7 when body `font-size` is set using
 *    `em` units.
 * 2. Prevent iOS text size adjust after orientation change, without disabling
 *    user zoom.
 */

html {
    font-size: 100%; /* 1 */
    -webkit-text-size-adjust: 100%; /* 2 */
    -ms-text-size-adjust: 100%; /* 2 */
}

/**
 * Address `outline` inconsistency between Chrome and other browsers.
 */

a:focus {
    outline: thin dotted;
}

/**
 * Improve readability when focused and also mouse hovered in all browsers.
 */

a:active,
a:hover {
    outline: 0;
}

/**
 * 1. Remove border when inside `a` element in IE 6/7/8/9 and Firefox 3.
 * 2. Improve image quality when scaled in IE 7.
 */

img {
    border: 0; /* 1 */
    -ms-interpolation-mode: bicubic; /* 2 */
}

/**
 * Address margin not present in IE 6/7/8/9, Safari 5, and Opera 11.
 */

figure {
    margin: 0;
}

/**
 * Correct margin displayed oddly in IE 6/7.
 */

form {
    margin: 0;
}

/**
 * Define consistent border, margin, and padding.
 */

fieldset {
    border: 1px solid #c0c0c0;
    margin: 0 2px;
    padding: 0.35em 0.625em 0.75em;
}

/**
 * 1. Correct color not being inherited in IE 6/7/8/9.
 * 2. Correct text not wrapping in Firefox 3.
 * 3. Correct alignment displayed oddly in IE 6/7.
 */

legend {
    border: 0; /* 1 */
    padding: 0;
    white-space: normal; /* 2 */
    *margin-left: -7px; /* 3 */
}

/**
 * 1. Correct font size not being inherited in all browsers.
 * 2. Address margins set differently in IE 6/7, Firefox 3+, Safari 5,
 *    and Chrome.
 * 3. Improve appearance and consistency in all browsers.
 */

button,
input,
select,
textarea {
    font-size: 100%; /* 1 */
    margin: 0; /* 2 */
    vertical-align: baseline; /* 3 */
    *vertical-align: middle; /* 3 */
}

/**
 * Address Firefox 3+ setting `line-height` on `input` using `!important` in
 * the UA stylesheet.
 */

button,
input {
    line-height: normal;
}

/**
 * Address inconsistent `text-transform` inheritance for `button` and `select`.
 * All other form control elements do not inherit `text-transform` values.
 * Correct `button` style inheritance in Chrome, Safari 5+, and IE 6+.
 * Correct `select` style inheritance in Firefox 4+ and Opera.
 */

button,
select {
    text-transform: none;
}

/**
 * 1. Avoid the WebKit bug in Android 4.0.* where (2) destroys native `audio`
 *    and `video` controls.
 * 2. Correct inability to style clickable `input` types in iOS.
 * 3. Improve usability and consistency of cursor style between image-type
 *    `input` and others.
 * 4. Remove inner spacing in IE 7 without affecting normal text inputs.
 *    Known issue: inner spacing remains in IE 6.
 */

button,
html input[type="button"], /* 1 */
input[type="reset"],
input[type="submit"] {
    -webkit-appearance: button; /* 2 */
    cursor: pointer; /* 3 */
    *overflow: visible;  /* 4 */
}

/**
 * Re-set default cursor for disabled elements.
 */

button[disabled],
html input[disabled] {
    cursor: default;
}

/**
 * 1. Address box sizing set to content-box in IE 8/9.
 * 2. Remove excess padding in IE 8/9.
 * 3. Remove excess padding in IE 7.
 *    Known issue: excess padding remains in IE 6.
 */

input[type="checkbox"],
input[type="radio"] {
    box-sizing: border-box; /* 1 */
    padding: 0; /* 2 */
    *height: 13px; /* 3 */
    *width: 13px; /* 3 */
}

/**
 * 1. Address `appearance` set to `searchfield` in Safari 5 and Chrome.
 * 2. Address `box-sizing` set to `border-box` in Safari 5 and Chrome
 *    (include `-moz` to future-proof).
 */

input[type="search"] {
    -webkit-appearance: textfield; /* 1 */ /* 2 */
    box-sizing: content-box;
}

/**
 * Remove inner padding and search cancel button in Safari 5 and Chrome
 * on OS X.
 */

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}

/**
 * Remove inner padding and border in Firefox 3+.
 */

button::-moz-focus-inner,
input::-moz-focus-inner {
    border: 0;
    padding: 0;
}

/**
 * 1. Remove default vertical scrollbar in IE 6/7/8/9.
 * 2. Improve readability and alignment in all browsers.
 */

textarea {
    overflow: auto; /* 1 */
    vertical-align: top; /* 2 */
}

/**
 * Remove most spacing between table cells.
 */

table {
    border-collapse: collapse;
    border-spacing: 0;
}

html,
button,
input,
select,
textarea {
    color: #222;
}

::selection {
    background: #b3d4fc;
    text-shadow: none;
}

img {
    vertical-align: middle;
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0;
}

textarea {
    resize: vertical;
}

.chromeframe {
    margin: 0.2em 0;
    background: #ccc;
    color: #000;
    padding: 0.2em 0;
}

a, a:hover, a:focus, a:active {
  text-decoration: none;
  color: inherit;
}

/* Layout */

#root{
  overflow: hidden;
}

.wrapper {
  overflow: hidden;
}

.main {
  display: grid;
  grid-template-columns: repeat(5 ,1fr);
  grid-gap: 10px 15px ;
  gap: 10px 15px ;
  background-color: rgb(240, 240, 240);
  padding: 10px 15px;
}

@media screen and (max-width: 992px) {
  .main {
    grid-template-columns: repeat(4, 1fr);
  }
}

@media screen and (max-width: 576px) {
  .main {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 400px) {
  .main {
    grid-template-columns: 1fr;
  }
}

.card {
  display: flex;
  flex-direction: column;
  background-color: white;
  box-shadow: 0 0 5px 1px rgba(128, 128, 128, .4);
}

.card__picture {
  width: 100%;
}

.card__img {
  width: 100%;
}

.card__title {
  padding: 15px 0;
  font-size: 20px;
  text-align: center;
}

.card__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  border-collapse: collapse;
  opacity: .3;
}

.card__item {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 10px 0;
  width: 100%;
  border: 2px solid rgba(128, 128, 128, .5);
  border-bottom: none;
  text-align: center;
}

.card__item:nth-child(3n+2) {
  border-right: none;
  border-left: none;
}

.card__item:nth-child(3n+1) {
  border-left: none;
}

.card__item:nth-child(3n+3) {
  border-right: none;
}

.moreInfo {
  font-weight: 900;
  font-size: 30px;
  
}

.moreInfo__link {
  display: block;
  text-decoration: none;
  color: gray;
  transition: .5s;
}

.moreInfo__link:hover {
  color: rgb(0, 38, 255);
}

.bookmark {
  max-width: 30px;
  max-height: 30px;
  transition: .3s;
  -webkit-filter: grayscale(1);
          filter: grayscale(1);
}

.bookmark-active {
  max-height: 30px;
  max-width: 30px;
  transition: .3s;
  -webkit-filter: grayscale(0);
          filter: grayscale(0);
}

/* Header */

.header{
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 50px;
  margin-bottom: 20px;
}

.header_booked {
  width: 70vw;
}

@media screen and (max-width:576px) {
  .header_booked {
    width: 100%;
  }
}

.logo {
  width: 100px;
  height: 100%;
}

.logo__picture {
  min-width: 100%;
  max-width: 100%;
  min-height: 100%;
  max-height: 100%;
}

/* SerachBar */

.search {
  width: 400px;
}

@media screen and (max-width: 576px) {
  .search {
    width: 40%;
  }
}

.search__form {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid rgba(204, 204, 204, .5);
}

.search__input{
  all: unset;
  font: 20px system-ui;
  padding: 5px 10px;
  height: 100%;
  width: 100%;
}

.search__input::-webkit-input-placeholder {
  color: black;
  opacity: .7;
}

.search__input:-ms-input-placeholder {
  color: black;
  opacity: .7;
}

.search__input::placeholder {
  color: black;
  opacity: .7;
}

.search__btn {
  all: unset;
  cursor: pointer;
  width: 40px;
  height: 40px;
}

svg {
  color: rgb(102, 68, 68);
  fill: currentColor;
  width: 24px;
  height: 24px;
  padding: 10px;
}

.favorite {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100px
}

.favorite__picture {
  max-width: 40px;
  max-height: 40px;
}

.loader {
  display: block;
  width: 200px;
  height: 200px;
  background: url(/serials-tracker/static/media/loader.ed84da1d.svg);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

/* Booked */

.booked__list {
  display: grid;
  grid-template-columns: 1fr;
  width: 70vw;
  grid-auto-rows: 90vh;
}

@media screen and (max-width: 992px) {
  .booked__list{
    grid-auto-rows: auto;
  }
}

@media screen and (max-width: 576px) {
  .booked__list{
    width: 100%;
    justify-content: center;
  }
}

.booked__box {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 2fr 1fr;
  grid-gap: 40px 0px;
  padding: 0 50px;
  box-shadow: 0 0 8px 1px rgb(190, 190, 190);
  width: 100%;
}

@media screen and (max-width: 992px) {
  .booked__box{
    padding: 0 15px;
    grid-template-rows: 3fr 1fr;
  }
}

@media screen and (max-width: 576px) {
  .booked__box{
    grid-template-rows: auto;
    overflow: hidden;
  }
}


.booked__container {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  grid-template-rows: 1fr 4fr;
  grid-gap: 20px 10px;
}

@media screen and (max-width: 992px) {

  .booked__container  {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 70px 2fr 1fr;
  }
}

@media screen and (max-width: 576px) {
  .booked__container  {
    grid-template-columns: 1fr;
    grid-template-rows: 60px 200px  repeat(2, minmax(150px, auto));
    justify-items: center;
    align-items: center;
  }
}


.booked__title {
  grid-column: 1/4;
  font-size: 50px;
  align-self: center;
  position: relative;
  width: -webkit-max-content;
  width: max-content;
}

@media screen and (max-width: 992px) {
  .booked__title {
    grid-column: 1/3;
    align-self: end;
  }
}

@media screen and (max-width: 576px) {
  .booked__title {
    grid-column: 1;
    font-size: 35px;
    align-self: center;
  }
}


.booked__title::before {
  content: '';
  display: block;
  width: 100%;
  box-shadow: 0px 1px 1px 01px #3c948b;
  position: absolute;
  top: 100%;
  left: 0;
}

.booked__picture {
  margin: auto;
  margin-left: 0;
  box-shadow:  5px 7px 10px 1px #3c948b;
}

@media screen and (max-width: 992px) {
 .booked__picture {
   grid-column: 1 ;
   grid-row: 2/3;
 }
}

@media screen and (max-width: 576px) {
  .booked__picture {
    margin: auto;
    max-width: 150px;
  }
 }

.booked__img {
  width: 100%;
  min-height: 100%;
  max-width: 250px;
  margin: auto;
}

.booked__descriprion {
  padding: 15px 0px;
}

@media screen and (max-width: 992px) {
    .booked__descriprion {
      grid-column: 1/3;
      grid-row: 3/4;
    }
}

@media screen and (max-width: 576px) {
  .booked__descriprion {
    grid-column: 1;
    grid-row: 3;
    font-size: 14px;
  }
}

.showInfo {
  background-color: rgb(240, 240, 240);
  box-shadow: 0 0 5px 1px rgba(128, 128, 128, .4);
  padding: 15px 10px;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

@media screen and (max-width: 992px) {
  .showInfo {
    grid-column: 2;
    grid-row: 2/3;
  }
}

@media screen and (max-width: 576px) {
  .showInfo {
    grid-column: 1;
    grid-row: 4/5;
  }
}

.showInfo__title {
  font-size: 30px;
  margin-bottom: 10px;
}

@media screen and (max-width: 576px) {
  .showInfo__title {
    font-size: 22px;
    margin-bottom: 5px;
  }
}

.showInfo__item {
  margin-bottom: 10px;
}

.showInfo__bold {
  font-weight: 900;
}

.episode {
  display: flex;
  flex-direction: column;
} 

.episode__title {
  font-size: 35px;
  margin-bottom: 15px;
}

.episode__box {
  display: block;
  flex-direction: column;
}
@media screen and (max-width: 576px) {
  .episode__box {
    padding-bottom: 20px;
  }
}


.episode__dateBox {
  position: relative;
  float: left;
  width: 50px;
  left: 0;
  top:0;
  border-radius: 2px;
  box-shadow: 0 1px 0 #bdbdbd, 0 2px 0 #fff, 0 3px 0 #bdbdbd, 0 4px 0 #fff, 0 5px 0 #bdbdbd, 0 0 0 1px #bdbdbd;
  margin: 5px 15px 15px ;
  text-align: center;
}

.episode__mounthYear {
  background-color: #3c948b;
  color: white;
  padding: 5px 0 10px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.episode__mounthYear::before {
  content: '';
  display: inline;
  height: 0px;
  width: 100%;
  position: absolute;
  bottom:  2px;
  left: 0px;
  border: 1px dashed white;
  
}

.episode__day {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 30px;
  padding-top: 5px;
  font-weight: 700;
}

.episode__name {
font-size: 30px;
}

.episode__info {
  font-size: 20px;
  line-height: 1.5;
}

.episode__description {
  grid-column-start: 2;
  grid-column-end: 9;
  grid-row: 4;
}

.rating {
  width: 100%;
  display: flex;
}

.rating__stars {
  width: 159px;
  position: relative;
  margin-right: 10px;
}

.rating__list, .rating__list-clone{ 
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.rating__list-clone{
  width: 50%;
  overflow: hidden;
}

.rating__star, .rating__star-clone  {
  min-width:  15px;
  min-height: 15px;
  margin-right: 1px;
  box-sizing: border-box;
  background-image: url(data:image/png;base64,);
  background-size: contain;
}

.rating__star:last-child , .rating__star-clone:last-child {
  margin-right: 0;
}

.rating__star-clone {
  background-image: url(data:image/png;base64,);
}

.rating__text {
  font-size: 16px;
  font-weight: 900;
}

/* navbar */

.navbar {
  height: 100%;
  background-color: rgb(233, 233, 233);
  position: fixed;
  top: 0;
  right: 1%;
  width: 25%;
  box-shadow: 0 0 8px 1px rgb(190, 190, 190);
  overflow-y: scroll;
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.navbar-active {
  width: 100vw;
  height: 100vh;
  overflow-y: scroll;
  background: rgba(90, 90, 90, 0.95);
  position: fixed;
  top: 0;
  left: 0;
}

@media screen and (max-width: 576px) {
  .navbar {
    display: none;
  }
}

.navbar::-webkit-scrollbar {
  display: none;
}

.navbar__title {
  font-size: 30px;
  padding: 10px 15px;
  margin-bottom: 15px;
  position: relative;
}

@media screen and (max-width: 576px) {
  .navbar__title {
    text-align: center;
    color: seashell;
  }
}

.navbar__title::before {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 10%;
  width: 80%;
  height: 1px;
  background-color: #3c948b;
}

.burger {
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 25px;
  height: 25px;
  position: fixed;
  top: 30px;
  left: 20px;
}

@media screen and (max-width: 576px){
  .burger {
    display: flex;
  }
}

.active .burger__row:nth-child(1) {
  position: absolute;
  top: 10px;
  left: 0;
  -webkit-transform: rotate(-135deg);
          transform: rotate(-135deg);
  width: 100%;
}

.active .burger__row:nth-child(2) {
  position: absolute;
  top: -20px;
  opacity: 0;
  width: 100%;
}

.active .burger__row:nth-child(3) {
  position: absolute;
  top: 10px;
  left: 0;
  -webkit-transform: rotate(135deg);
          transform: rotate(135deg);
  width: 100%;
}

.burger__row {
  height: 4px;
  border-radius: 3px;
  background: #3c948b;
  transition: 0.5s;
  top: 0;
}

.ant-anchor-link {
transition: 0.3s;
padding-left: 15px;
}

@media screen and (max-width: 576px) {
  .ant-anchor-link {
     padding: 0;
     font-size: 20px;
     text-align: center;
     color: seashell;
     margin-bottom: 10px;
  }
}

.ant-anchor-link-active {
  background-color: #3c948b !important;
}

.ant-anchor-link-title {
  display: block;
  line-height: 1.5;
}

.ant-anchor-link:hover {
  background-color: rgb(209, 209, 209);
}

/* popup */

.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .7);
}

.popup__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 400px;
  height: 300px;
  background-color: white;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  padding-bottom: 20px;
  overflow: hidden;
}

.popup__text {
  text-align: center;
  padding: 50px 20px;
  background-color: rgb(137, 228, 84)
}

.popup__btn {
  width: 40px;
  height: 40px;
  background-color: black;
  border-radius: 50%;
  outline: none;
  position: relative;
  border: 2px solid transparent;
  transition: .5s;
  }
  
  .popup__btn::after, .popup__btn::before {
    content: '';
    position: absolute;
    width: 30%;
    height: 2px;
    background-color: rgb(124, 124, 124);
    left: 50%;
    top: 50%;
    transition: .5s;
  }
  
  .popup__btn::after {
    -webkit-transform: translate(-50%, -50%) rotate(45deg);
            transform: translate(-50%, -50%) rotate(45deg);
  }
  
  .popup__btn::before{
    -webkit-transform: translate(-50%, -50%) rotate(-45deg) ;
            transform: translate(-50%, -50%) rotate(-45deg) ;
  }
  
  .popup__btn:hover {
    border-color: teal;
  }
  
  .popup__btn:hover::before , .popup__btn:hover::after {
   width: 60%;
   background-color: teal;
  }

  
.popup {
  width: 1vw;
  height: 1vh;
  opacity: 0;
  z-index: -1;
  transition: opacity .5s;
}

.popup__container {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  transition: .5s;
}
.popup-enter-active {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 1;
}

.popup-enter-active .popup__container{
  
  -webkit-transform: translateX(0%);
  
          transform: translateX(0%);
}

.popup-exit-active {
  width: 100vw;
  height: 100vh;
  opacity: 0;
  z-index: 1;
}

.popup-exit-done {
  width: 1vw;
  height: 1vh;
  opacity: 0;
  z-index: -1;
}

.popup-exit-active .popup__container {
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
}

.popup-enter-done {
  width: 100vw;
  height: 100vh;
  opacity: 1;
  z-index: 1;
}

.popup-enter-done .popup__container {
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

